/* Game.css */

.game {
    text-align: center;
    padding: 2rem;
}

.game h1 {
    font-size: 2.5rem;
    margin-bottom: 2rem;
}

.guess-buttons {
    margin: 1rem 0;
}

.guess-buttons button {
    background-color: #1976d2;
    color: #fff;
    border: none;
    padding: 0.75rem 1.5rem;
    margin: 0 0.5rem;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.guess-buttons button:hover {
    background-color: #1565c0;
}

.guess-buttons button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

.loading {
    font-size: 1.2rem;
    margin: 1rem 0;
}

.results {
    margin-top: 2rem;
}

.results h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.results p {
    font-size: 1.1rem;  /* Ensure consistent font size */
}

.scoreboard {
    margin-top: 2rem;
}

.scoreboard h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.scoreboard p {
    font-size: 1.1rem;
}

.emoji-scoreboard {
    margin-top: 2rem;
    text-align: center;
}

.emoji-scoreboard h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.emoji-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.emoji {
    font-size: 2rem;
    margin: 0.5rem;
}

.results-history {
    margin-top: 2rem;
}

.results-history h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.results-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.result-entry {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 1rem;
    font-size: 1.2rem;
    border-bottom: 1px solid #ddd;
    padding-bottom: 0.5rem;
    width: 100%;
    max-width: 600px;
}

.result-entry .emoji {
    font-size: 2rem;
    margin: 0 0.5rem;
}

.result-entry .label {
    margin: 0 0.5rem;
    font-weight: bold;
}

.result-entry .guess {
    margin: 0 0.5rem;
    color: #555;
}

.result-entry .vs {
    margin: 0 1rem;
    font-weight: bold;
}

.result-entry .confidence {
    margin-left: auto;
    font-size: 0.9rem;
    color: #777;
    text-align: right;
    flex-basis: 100%;
    margin-top: 0.5rem;
}

/* Define the shake animation */
@keyframes shake {
    0% { transform: translateX(0); }
    25% { transform: translateX(-5px); }
    50% { transform: translateX(5px); }
    75% { transform: translateX(-5px); }
    100% { transform: translateX(0); }
  }
  
  /* Apply the shake animation to the .shake class */
  .shake {
    animation: shake 0.5s;
    display: inline-block;
  }
  